.App {
  background-color: #282c34;
  color: white;
  height: 100vh;
}

.videoPlayer{
  height: 360px;
  width: 640px;
  display: flex;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
